.scene-container {
  width: 100%;
  height: 100%;
  bottom: 0;
  display: flex;
  justify-content: center;
  position: absolute;
  align-items: flex-end; }

.card1 {
  top: 50%;
  align-self: center;
  position: absolute;
  border: 8px solid #f0ebc5;
  will-change: transform;
  width: 37vw;
  height: 35vh;
  background: radial-gradient(#fff 30%, #fcfae4 100%);
  animation: callOfLions 10s infinite linear; }
  @media (min-width: 768px) {
    .card1 {
      top: 43%;
      width: 27vw; } }
  @media all and (device-width: 1024px) and (device-height: 768px) and (orientation: landscape) {
    .card1 {
      top: 33%;
      width: 17vw; } }
  @media (min-width: 1200px) {
    .card1 {
      top: 20%;
      height: 60vh;
      width: 17vw; } }
  .card1::after {
    content: '';
    position: absolute;
    width: 173%;
    height: 120%;
    top: 0;
    left: -35%;
    background: transparent url("/portal2.png") no-repeat;
    background-size: 100%;
    background-position: center;
    animation: rotating 5s linear infinite; }

@keyframes rotating {
  0% {
    filter: brightness(1);
    transform: rotate(0deg); }
  50% {
    filter: brightness(1.2);
    transform: rotate(180deg); }
  100% {
    filter: brightness(1);
    transform: rotate(360deg); } }

.card2 {
  will-change: transform;
  background-image: url("/lions-web.png");
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: 160%;
  height: 60%;
  bottom: -10%;
  width: 100%; }
  @media (min-width: 768px) {
    .card2 {
      background-size: 120%; } }
  @media (min-width: 992px) {
    .card2 {
      background-size: contain; } }

@keyframes callOfLions {
  0% {
    background: radial-gradient(#fff 0%, #fcfae4 100%);
    box-shadow: 0 0 2px #fff, 0 0 2px #fff, 0 0 3px yellow, 0 0 4px #fcfae4, 0 0 5px #fcfae4; }
  50% {
    background: radial-gradient(#fff 30%, #fcfae4 100%);
    box-shadow: 0 0 20px #fff, 0 0 30px #fff, 0 0 40px #fff, 0 0 50px #fff, 0 0 60px #fff; }
  100% {
    background: radial-gradient(#fff 0%, #fcfae4 100%);
    box-shadow: 0 0 2px #fff, 0 0 2px #fff, 0 0 3px yellow, 0 0 4px #fcfae4, 0 0 5px #fcfae4; } }

.scene {
  height: 100vh;
  position: relative;
  overflow: hidden;
  background: radial-gradient(ellipse at bottom, #ea526a 0%, #2d1d46 70%, #090a0f 100%); }
  .scene::after {
    content: '';
    position: absolute;
    background: radial-gradient(transparent 0%, #000 250%);
    height: 100%;
    width: 100%;
    pointer-events: none;
    top: 0; }
  .scene::before {
    content: ' ';
    position: absolute;
    background: linear-gradient(to top, #000 20%, transparent);
    height: 20%;
    width: 100%;
    pointer-events: none;
    bottom: 0;
    z-index: 1; }
  .scene canvas {
    background-image: url("/bg_nebula.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh !important; }
  .scene .scene-landscape {
    background-image: url("/bg_landscape-web.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 100%;
    bottom: 0;
    width: 100%;
    position: absolute;
    filter: blur(1px); }
